.form-widget {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  font-family: system-ui, -apple-system, sans-serif;
}

.form-container {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

form div {
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  font-weight: 500;
  display: block;
}

input {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background: #0070f3;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

button:hover {
  background: #0051cc;
}
